@import url(https://fonts.googleapis.com/css2?family=Space+Mono:wght@400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Space+Mono:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/**/

body {
  background-color: hsl(185, 41%, 84%);
}

.App {
  text-align: center;
}

.App h1 {
  color: hsl(183, 100%, 15%);
  font-family: 'Space Mono', monospace;
  font-size: 1.4em;
  letter-spacing: .3em;
}
/**/

.main-container {
    background-color: white;
    border-radius: 20px;
    font-family: 'Space Mono', monospace;
    padding: 1em 2em;
    width: 375px;
}

.main-container h2 {
    color: hsl(186, 14%, 43%);
    font-size: 1rem;
}

.center {
    display: flex;
    justify-content: center;
}

.form-input {
    flex-direction: column;
    font-weight: 700;
    text-align: left;
}

.form-input label {
    color: hsl(186, 14%, 43%);
}

.form-input input {
    background-color: hsl(189, 41%, 97%);
    border: none;
    border-radius: 4px;
    color: hsl(183, 100%, 15%);
    font-family: 'Space Mono', monospace;
    font-size: 1.4em;
    font-weight: 700;
    height: 2em;
    margin: .4em 0;
    padding-right: 14px;
    text-align: right;
}

.form-input input:focus {
    outline-color: hsl(172, 67%, 45%);
}

.brown-outline:focus {
    outline-color: hsl(27, 61%, 56%) !important;
}

.zero-text {
    color: hsl(27, 61%, 56%);
    margin-left: 22%;
    opacity: 0;
}

.show {
    opacity: 1 !important;
}

.icon {
    color: hsl(184, 14%, 56%);
    font-size: 1.5em;
    font-weight: 400;
    margin-top: 20px;
    padding: 0 16px;
    position: absolute;
}

.icon.p {
    margin-top: 12px;
}

.tip-grid {
    display: grid;
    grid-gap: 1em;
    grid-template-columns: 1fr 1fr;
    margin-bottom: 1.8em;
}

@media (min-width: 1200px) {
    .main-container {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        width: 75%;
    }
    .f-width {
        display: flex;
        flex-direction: column;
        height: 97%;
        justify-content: space-between;
        width: 50%;
    }
    .make-row {
        height: 100%;
        margin-left: 6em;
        width: 50%;
    }
    .tip-grid {
        grid-template-columns: repeat(3, 1fr);
    }
    .zero-text {
        margin-left: 33%;
    }
}
.t-button {
    border-radius: 6px;
    font-size: 1.4em;
    padding: 10px 16px;
    text-align: center;
}

.t-button:hover:not(.cus) {
    background-color: hsl(172, 72%, 70%);
    color: hsl(183, 100%, 15%);
    cursor: pointer;
}

.cus:hover {
    color: white;
}

.reg {
    background-color: hsl(183, 100%, 15%);
    color: white;
}

.cus {
    background-color: hsl(189, 41%, 97%);
    border-radius: 6px;
    color: hsl(186, 14%, 43%);
    font-size: 1.4em;
}

.cus input {
    font-size: 1em;
    height: 100%;
    margin: 0;
    padding-right: 14px;
    text-indent: -66px;
    width: 90%;
}

input::-webkit-outer-spin-button, input::-webkit-inner-spin-button {
    display: none;
}
.result-grid {
    background-color: hsl(183, 100%, 15%);
    border-radius: 16px;
    display: grid;
    grid-template-areas: "box1 result1" "box2 result2" "button button";
    margin: 1.2em 0;
    padding: 1.4em 2em;
}

.result-grid h2 {
    color: hsl(172, 67%, 45%);
    font-size: 2em;
}

.result-grid h3 {
    color: hsl(189, 41%, 97%);
    font-size: 1em;
    margin-bottom: 4px;
}

.result-grid p {
    color: hsla(189, 41%, 97%, .5);
    font-size: .8em;
    margin-top: 2px;
}

.result-grid button {
    background-color: hsl(172, 67%, 45%);
    border: none;
    border-radius: 6px;
    color: hsl(183, 100%, 15%);
    font-family: 'Space Mono', monospace;
    font-size: 1.2em;
    font-weight: 700;
    height: 2.5em;
    letter-spacing: 0;
}

.result-grid button:hover {
    background-color: hsl(172, 72%, 70%);
    cursor: pointer;
}

.a {
    grid-area: box1;
}

.b {
    grid-area: box2;
}

.c {
    grid-area: result1;
    text-align: right;
}

.d {
    grid-area: result2;
    text-align: right;
}

.no-top {
    margin-top: .4em;
}

.e {
    grid-area: button;
}

@media (min-width: 1200px) {
    .result-grid {
        padding-top: 3em;
    }
    .e {
        margin-top: 6em;
    }
}
